import React,{useEffect,useState} from 'react'
import {Grid,IconButton,Typography,Tooltip,Box,Paper,Accordion,AccordionSummary,AccordionDetails,Button} from '@material-ui/core'
import Icon from '@material-ui/core/Icon'
import {useDispatch,useSelector} from 'react-redux'
import {fetchTodo,deleteTodoById,getTodoById,selectTodo} from '../store/actions/todoActions'
import {initialState} from '../store/reducers/todoReducer'
import {deleteInfoMessage,clearTodoListMessage} from '../utils'

const TodoList = (props) => {

    const dispatch = useDispatch()
    
    const todos = useSelector(state => state.todos.todos)

    const currentTodo = useSelector(state => state.todos.currentTodo)

    const loading = useSelector(state => state.todos.loading)
    
    const [expanded,setExpand] = useState(false)

    const [visible,setVisible] = useState("hidden")

    setTimeout(() => {

        setVisible('visible')
        
    }, 1000);

    useEffect(() => {
       
        dispatch(fetchTodo())
    
    },[dispatch])

    const handleToggleAccordion = (panel) => (event,isExpanded) => {

        setExpand(isExpanded ? panel : false)
    }

    const handleDelete = (id) => {

        if(currentTodo._id === id){ dispatch(selectTodo(initialState.currentTodo))}
        
        dispatch(deleteTodoById(id,deleteInfoMessage))

        props.openTodoForm(false)

        setExpand(false)
    }

    const handleEditTodo =  (id) => {

        const editModeForm = "Edit"

        dispatch(getTodoById(id))

        props.handleModeForm(editModeForm)

        props.openTodoForm(true)

        setExpand(false)
    }

    const deleteAllTodo = () => {

        todos.forEach(todo => dispatch(deleteTodoById(todo._id,clearTodoListMessage)))
    }

    return( 
        <Grid item md={8} >

            { !loading ? (todos.length >= 0 && <Paper style={{visibility:visible}}>

                {todos.map((todo,index) =>

                    <Accordion style={{backgroundColor:todo.color}} expanded={expanded === index} onChange={handleToggleAccordion(index)} key={index} >
                        <AccordionSummary
                        expandIcon={<Icon>expand_more</Icon>}
                        >
                            <Typography>{todo.title}</Typography>
                        </AccordionSummary>
                        <AccordionDetails>
                            <Grid container justify="space-between" alignItems="center">
                                <Box>
                                    <Typography component="p" variant="body2">{todo.description}</Typography>
                                </Box>
                                <Box>
                                    <Tooltip title="edit your task">
                                        <IconButton onClick={()=> handleEditTodo(todo._id)} size="small"><Icon className="iconStyle" >edit</Icon></IconButton>
                                    </Tooltip>
                                    <Tooltip title="delete your task">
                                        <IconButton size="small" onClick={() => handleDelete(todo._id)}><Icon className="iconStyle">delete_forever</Icon></IconButton>
                                    </Tooltip>
                                </Box>
                            </Grid>
                        </AccordionDetails>
                    </Accordion>    
                )}
            </Paper>) :(null)}

            {!loading ? (todos.length > 0 &&
                 <Box  display="flex" justifyContent="center" style={{visibility:visible}}>
                 <Button onClick={deleteAllTodo} size="large" startIcon={<Icon  className="iconStyle">delete_outlined</Icon>}>
                     <Typography  className="buttonFont" >Clear ToDo List</Typography>
                 </Button>
             </Box>
            ) : (null)} 
        </Grid>
    )
}

export default TodoList