import React from 'react';
import {useSelector,useDispatch} from 'react-redux'
import {initialState} from '../store/reducers/todoReducer'
import {setAlert} from '../store/actions/todoActions'
import Alert from '@material-ui/lab/Alert';



const AlertMessage = () => {

  const alert = useSelector(state => state.todos.alert)

  const dispatch = useDispatch()

  const clearAlert = () => dispatch(setAlert(initialState.alert))

  const RenderAlert = () => {

    if(alert.message !== ""){

      setTimeout(() => {

        clearAlert()
        
      }, 3000);

      return(
        <Alert variant="filled" severity={alert.severity}>
          {alert.message}
        </Alert>
      )
    }
    return null
  }

  return (
    <div >
      {RenderAlert()}
    </div>
  );
}

export default AlertMessage