import React,{useState} from 'react'
import {Card, CardContent,Typography,CardActions,Button,Box,TextField,IconButton,Grid,MenuItem} from '@material-ui/core'
import {useDispatch,useSelector} from 'react-redux'
import {patchTodo, postTodo,setAlert} from '../store/actions/todoActions'
import {addSuccessAlert,updateSuccessAlert,errorAlert} from '../utils'
import {colors} from '../utils'
import Icon from '@material-ui/core/Icon'


const TodoForm = (props) => {

    const dispatch = useDispatch()

    const currentTodo = useSelector(state => state.todos.currentTodo)

    const [todo,setTodo] = useState(currentTodo)

    const [modeFormName,setModeFormName] = useState(props.modeForm)


    React.useEffect(() => {

        setTodo(currentTodo)

        setModeFormName(props.modeForm)

    },[setTodo,currentTodo,setModeFormName,props.modeForm])

    const handleChange = (e) => {

        const {value,name} = e.target

        setTodo({...todo,[name]:value})
    }

    const handleSubmit = () => {

        if(todo.title === "" || todo.description === "" || todo.color === ""){

            dispatch(setAlert(errorAlert))

        } else {
            
            modeFormName === "Create" ?  dispatch(postTodo(todo,addSuccessAlert)) : dispatch(patchTodo(todo,updateSuccessAlert))
            
            props.closeTodoForm()
            window.sessionStorage.setItem('open','true')
        }
    }

    const handleCancel = () => setTodo(currentTodo)

    return(

        <Box mt={2} width="90%">
            <Card className="backGroundCard" elevation={4}>
                <CardContent>
                    <Box>
                        <Typography variant="h4" className="subTitle1" >{modeFormName} your task</Typography>
                    </Box>
                    <Box display="flex" flexDirection="column">
                        <TextField 
                            onChange={handleChange}
                            color="secondary" 
                            margin="dense" 
                            label="Title"
                            name="title"
                            value={todo.title}
                            required
                            />
                        <TextField
                            onChange={handleChange}
                            color="secondary"
                            id="filled-multiline-static"
                            label="Description"
                            multiline
                            rows={1}
                            value={todo.description}
                            name="description"
                            variant="filled"
                            required
                        />
                          <TextField
                            onChange={handleChange}
                            required
                            id="standard-select-color"
                            select
                            label="Color"
                            name="color"
                            value={todo.color}
                            helperText="Select a color for your task"
                            >
                            {colors.map((option) => (
                                <MenuItem key={option.value} value={option.value}>
                                    <Box display="flex" justifyContent="space-between" style={{width:"100%"}}>
                                        <Typography>{option.label}</Typography> 
                                        <Box className="colorPrev" style={{backgroundColor:option.value}}></Box>
                                    </Box>
                                </MenuItem>
                            ))}
                            </TextField>
                    </Box>
                </CardContent>
                <CardActions>
                    <Grid container display="flex" justify="space-between" alignItems="baseline" >
                        <Box>
                            <Button onClick={handleSubmit} size="small"><Typography className="buttonFont" >Save</Typography></Button>
                            <Button onClick={handleCancel} size="small"><Typography className="buttonFont" >Cancel</Typography></Button>
                        </Box>
                        <Box>
                             <IconButton onClick={() => props.closeTodoForm(false)}>
                                <Icon className="iconStyle">close</Icon>
                            </IconButton>
                        </Box>
                    </Grid>
                </CardActions>
            </Card>
            
        </Box>
    )
}


export default TodoForm