import axios from 'axios'

export const GET_TODOS = "GET_TODOS"
export const CURRENT_TODO = "CURRENT_TODO"
export const ADD_TODO = "ADD_TODO"
export const DELETE_TODO = "DELETE_TODO"
export const SET_ALERT = "SET_ALERT"
export const UPDATE_TODO = "UPDATE_TODO"
export const SET_LOADING = "SET_LOADING"


export const getTodos = (todos) => ({

    type: GET_TODOS,
    payload:todos
})

export const selectTodo = (todo) => ({

    type : CURRENT_TODO,
    payload:todo
})

export const addTodo = (todo) => ({

    type:ADD_TODO,
    payload:todo
});

export const deleteTodo = (id) => ({

    type:DELETE_TODO,
    payload:id
});

export const updateTodo = (todo) => ({

    type:UPDATE_TODO,
    payload:todo
});

export const setAlert = (alert) => ({

    type:SET_ALERT,
    payload:alert
});

export const isLoading = (mode) => ({

    type:SET_LOADING,
    payload:mode
})

export const fetchTodo = () =>  async dispatch => {

    const url = "https://todo.eachbase.com/api/RaffiHaycan/todos"

        dispatch(isLoading(true))

        try {

            const response = await axios.get(url)

            const data = await response.data

            dispatch(getTodos(data))
            dispatch(isLoading(false))

        } catch (error) {console.log(error)}
    
}

export const getTodoById = (payload) => async dispatch => {

   const url =  `https://todo.eachbase.com/api/RaffiHaycan/todos/${payload}`

    try {

        const response = await axios.get(url)

        const result =  await response.data

        dispatch(selectTodo(result))
        
    } catch (error) {console.log(error) }

}

export const postTodo = (payload,message) => async dispatch => {

    const url = "https://todo.eachbase.com/api/RaffiHaycan/todos"

    try {

       const response =  await axios.post(url,payload)

       const result =  await response.data

       dispatch(addTodo(result))
       dispatch(setAlert(message))

    } catch (error) {console.log(error)}

}

export const deleteTodoById = (payload,message) => async dispatch => {

    const url = "https://todo.eachbase.com/api/RaffiHaycan/todos/" + payload;

    dispatch(isLoading(true))

    try {
        await axios.delete(url,{data:payload})

        dispatch(deleteTodo(payload))
        dispatch(setAlert(message))

        dispatch(isLoading(false))

    } catch (error) {console.log(error)}
}

export const patchTodo = (payload,message) => async dispatch => {

    const url = "https://todo.eachbase.com/api/RaffiHaycan/todos/" + payload._id;

    try {

        const response = await axios.patch(url,payload)

        const result = await response.data

        dispatch(updateTodo(result))
        dispatch(setAlert(message))

    } catch (error) {console.log(error)}
}

