import React,{useState} from 'react'
import TodoForm from './TodoForm'
import {Grid,Typography,Button,Box} from '@material-ui/core'
import Icon from '@material-ui/core/Icon'
import TodoList from './TodoList'
import {useDispatch} from 'react-redux'
import {selectTodo} from '../store/actions/todoActions'
import {initialState} from '../store/reducers/todoReducer'
import AlertMessage from './Alert'


const TodoHeader = () => {

    const dispatch = useDispatch()

    const createModeForm = "Create";

    const [toggleTodoForm,setToggleTodoForm] = useState(false)
    const [modeForm,setFormMode] = useState(createModeForm)

    const handleToggleTodoForm = (val) => setToggleTodoForm(val)

    const handleModeForm = (val) => setFormMode(val)

    const handleFormState = (stateForm,modeFormName) => {

        dispatch(selectTodo(stateForm))
        handleModeForm(modeFormName)
        handleToggleTodoForm(true)
    }
    
    return( 

      <Grid container justify="flex-end">
       
        <Grid item md={7}>
            <Grid container  justify="center" direction="column" alignItems="center">
                <Box justify="center">
                    <Typography variant="h2" className="title">ToDo</Typography>
                </Box>
                <Box justify="center">
                    <Typography variant="h5" className="subTitle">Your daily task app !</Typography>
                </Box>
                <Box onClick={() => handleFormState(initialState.currentTodo,createModeForm)} mt={1}>
                    <Button size="large" startIcon={<Icon  className="iconStyle">add_circle_outline</Icon>}>
                        <Typography  className="buttonFont" >add a new task</Typography>
                    </Button>
                </Box>
            </Grid>
            <Grid  container  justify="center">
                <TodoList handleModeForm={handleModeForm} openTodoForm={handleToggleTodoForm}/>
            </Grid>
        </Grid>
        <Grid item md={3}> 
           {toggleTodoForm && <TodoForm modeForm={modeForm} closeTodoForm={handleToggleTodoForm}/>} 
           <Box mt={7} mr={2}>
                <AlertMessage/>
            </Box>
        </Grid>
    </Grid>
        
    )
}

export default TodoHeader