import  '../actions' 
import actions from '../actions'

export const initialState = {

    todos: [],

    currentTodo : {title:'',description:'',color:""},

    alert: {message :"", severity:""},

    loading: false
}

export const todoReducer = (state = initialState,action) => {

    switch(action.type){

        case actions.todoActions.ADD_TODO:
            return {...state,
                todos:[...state.todos,action.payload]}
        
        case actions.todoActions.GET_TODOS:
            return {...state,
                todos:state.todos.concat(action.payload)}

        case actions.todoActions.DELETE_TODO:
            return {...state,
                todos: state.todos.filter(todo => todo._id !== action.payload)}

        case actions.todoActions.UPDATE_TODO:
            return{...state,
                todos: state.todos.map(todo => todo._id === action.payload._id ? action.payload : todo)}

        case actions.todoActions.CURRENT_TODO:
            return {...state,
                currentTodo:action.payload}
        
        case actions.todoActions.SET_ALERT:
            return {...state,alert:action.payload}

        case actions.todoActions.SET_LOADING:
            return {...state,loading:action.payload}

        default: 
        return state
    };
}


