import React from 'react'
import {Grid} from '@material-ui/core'
import TodoHeader from './components/TodoHeader'
import {useSelector,useDispatch} from 'react-redux'
import {deleteTodoById} from './store/actions/todoActions'
import {initialState} from './store/reducers/todoReducer'

function App() {  

  const dispatch = useDispatch()

  const todos = useSelector(state => state.todos.todos)

  React.useEffect(() => {

    if(!window.sessionStorage.getItem('open')){

      todos.forEach(todo => {
        
        dispatch(deleteTodoById(todo._id,initialState.alert))
      });
    }

  },[dispatch,todos])

  return (
    <Grid container className="backGround" justify="center">
        <Grid item md={12}>
            <Grid container justify="center">
              <TodoHeader/>
            </Grid>
        </Grid>
    </Grid>
  )
}

export default App;
